<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col sm="6" lg="3">
        <b-card :no-body="true" footer-class="px-3 py-2">
          <b-card-body class="p-3 clearfix">
            <i class="fa fa-server bg-primary p-3 font-2xl mr-3 float-left"></i>
            <div class="h5 text-primary mb-0 mt-2">{{orderCount}}</div>
            <div class="text-muted text-uppercase font-weight-bold font-xs">Orders</div>
          </b-card-body>
          <div slot="footer">
            <b-link class="font-weight-bold font-xs btn-block text-muted" :href="routeTo('Orders')">
              View More
              <i class="fa fa-angle-right float-right font-lg"></i>
            </b-link>
          </div>
        </b-card>
      </b-col>
      <b-col sm="6" lg="3">
        <b-card :no-body="true" footer-class="px-3 py-2">
          <b-card-body class="p-3 clearfix">
            <i class="fa fa-book bg-primary p-3 font-2xl mr-3 float-left"></i>
            <div class="h5 text-primary mb-0 mt-2">{{productCount}}</div>
            <div class="text-muted text-uppercase font-weight-bold font-xs">Products</div>
          </b-card-body>
          <div slot="footer">
            <b-link
              class="font-weight-bold font-xs btn-block text-muted"
              :href="routeTo('Products')"
            >
              View More
              <i class="fa fa-angle-right float-right font-lg"></i>
            </b-link>
          </div>
        </b-card>
      </b-col>
      <b-col sm="6" lg="3">
        <b-card :no-body="true" footer-class="px-3 py-2">
          <b-card-body class="p-3 clearfix">
            <i class="fa fa-user-o bg-primary p-3 font-2xl mr-3 float-left"></i>
            <div class="h5 text-primary mb-0 mt-2">{{customerCount}}</div>
            <div class="text-muted text-uppercase font-weight-bold font-xs">Customers</div>
          </b-card-body>
          <div slot="footer">
            <b-link
              class="font-weight-bold font-xs btn-block text-muted"
              :href="routeTo('Customers')"
            >
              View More
              <i class="fa fa-angle-right float-right font-lg"></i>
            </b-link>
          </div>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { Callout } from "@coreui/vue";

export default {
  name: "dashboard",
  components: {
    Callout
  },
  data: function() {
    return {
      orderCount: 0,
      customerCount: 0,
      productCount: 0
    };
  },
  mounted: function() {
    var { counterService } = this.$fbServices;
    counterService.getOrderCount().then(c => (this.orderCount = c));
    counterService.getCustomerCount().then(c => (this.customerCount = c));
    counterService.getProductCount().then(c => (this.productCount = c));
  },

  methods: {
    routeTo: function(to) {
      return this.$router.resolve({ name: to }).href;
    }
  }
};
</script>

<style scoped>
</style>
